/* ---------------------------------------Add Task Page------------------------------------------------------- */

.add-tasks-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.create-task-btn {
  width: 100px;
  height: 100px;
  background: #e07290;
  border: none;
  border-radius: 50%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 1.75;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.create-task-btn:hover {
  background: #356cb1;
  box-shadow: 1px 1px 7px 1px #505050;
  margin-top: -2px;
}

.modal {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal-content {
  padding: 20px;
  border-radius: 12px;
  background-color: white;
  transform: scale(0.5);
  transition: 0.4s all;
}

.modal-content.active {
  transform: scale(1);
}

.title {
  margin-bottom: 60px;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 1.75;
  text-align: center;
  color: #ffffff;
}

.app-nav {
  max-width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;
  background-color: #e07290;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.app-nav-container {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 15px;
}

.app-headline {
  font-size: 26px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;
  margin-right: 150px;
  position: absolute;
}

.nav-list {
  display: flex;
  margin: 0 auto;
}

.nav-point {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;
}

.nav-point:hover {
  text-shadow: 1px 7px 10px #511e4f;
  margin-top: -2px;
}

.nav-point:not(:last-child) {
  margin-right: 20px;
}

.for-link {
  text-decoration: none;
  color: inherit;
}
@media (max-width: 530px) {
  .nav-list {
    margin: 0 0 0 auto;
  }
}
@media (max-width: 410px) {
  .nav-point {
    font-size: 14px;
  }
  .app-headline {
    font-size: 20px;
  }
}

.create-modal {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.create-modal-text {
  margin-bottom: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 1.75;
  text-align: center;
  color: #1e3266;
}

.create-modal-textarea {
  width: 100%;
  height: 160px;
  border-radius: 10px;
  margin-bottom: 40px;
  padding: 5px;
  font-size: 20px;
  resize: none;
  border: 1px solid #1e3266;
}

.active-textarea {
  border: 1px solid #007bff;
}

.create-modal-textarea:invalid {
  border: 4px solid red;
}

.create-modal-btn {
  width: 120px;
  height: 30px;
  background: #1e3266;
  border: none;
  border-radius: 13px;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 1.75;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.create-modal-btn:hover {
  background: #356cb1;
  box-shadow: 1px 1px 7px 1px #505050;
}

._disable {
  background: #88888a;
  border: 1px solid #ebebeb;
  box-sizing: border-box;
}

.change-modal {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.change-modal-text {
  margin-bottom: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 1.75;
  text-align: center;
  color: #1e3266;
}

.change-modal-textarea {
  width: 100%;
  height: 160px;
  border-radius: 10px;
  margin-bottom: 40px;
  padding: 5px;
  font-size: 20px;
  resize: none;
  border: 1px solid #1e3266;
}

.change-modal-btn {
  width: 120px;
  height: 30px;
  background: #ffa601;
  border: none;
  border-radius: 13px;
  font-style: normal;
  font-weight: bold;
  font-size: 13px;
  line-height: 1.75;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.change-modal-btn:hover {
  background: #ffcb6a;
  box-shadow: 1px 1px 7px 1px #505050;
}

.tasks-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 25px;
  gap: 25px;
  margin-bottom: 40px;
  padding: 0 20px;
}

.created-task-card {
  height: 100%;
  width: 300px;
  background: #6182ac;
  border: 2px solid white;
  box-sizing: border-box;
  border-radius: 35px;
  display: flex;
  flex-direction: column;
  padding: 25px;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.75;
  text-align: left;
  color: white;
}

.created-task-card:hover {
  margin-top: -2px;
}

.card-text {
  margin-bottom: 20px;
}

.card-btns {
  display: flex;
  justify-content: space-between;
}

.change-btn {
  width: 90px;
  height: 30px;
  background: #ffa601;
  border: none;
  border-radius: 13px;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 1.75;
  color: #1e3266;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.delete-btn {
  width: 90px;
  height: 30px;
  background: #ff3d2f;
  border: none;
  border-radius: 13px;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 1.75;
  color: #1e3266;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.change-btn:hover {
  background: #ffcb6a;
  box-shadow: 1px 1px 7px 1px #505050;
  margin-top: -2px;
}

.delete-btn:hover {
  background: #fd6e64;
  box-shadow: 1px 1px 7px 1px #505050;
  margin-top: -2px;
}

